const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			checkedAddress: {},
			addrList: [],
			currage: 1,
			list: [],
			isDialogShow: false,
			active_id: '',
			remark: ''
		}
	},
	//初始化
	onLoad() {
		// let pages = getCurrentPages();
		// let { options } = pages.pop();
		// if(options.id) {
		// 	this.currage = options.id;
		// 	if(this.currage == 1) {
		// 		this.getTreaList();
		// 	}else if(this.currage == 2) {
		// 		this.getZeroList();
		// 	}
		// }
		this.getTreaList();
	},
	// 页面显示
	onShow() {
		
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/user/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取默认地址
		getAddrList() {
			util.request(api.getAddreList,{},'get').then(res=>{
				if(res.code == 0) {
					this.addrList = res.data.list;
					this.addrList.forEach(item=>{
						if(item.is_default == 1) {
							this.checkedAddress = item;
						}
					})
					
				}
			})
		},
		//获取夺宝记录
		getTreaList() {
			wx.showLoading()
			util.request(api.getTreaRecordList,{page: 1,size:200},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					this.list = res.data.list;
				}
			})
		},
		//tab切换
		changeTab(index) {
			if(index == this.currage) {
				return false;
			}else {
				this.currage = index;
				if(index == 1) {
					this.getTreaList()
				}else {
					this.getZeroList()
				}
			}	
		},
		//获取免费抢购记录
		getZeroList() {
			wx.showLoading()
			util.request(api.getZeroRecordList,{page: 1,size:200},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					this.list = res.data.list;
				}
			})
		},
		//选择收货地址
		selectAddress() {
			wx.navigateTo({
				url: '/pages/lottery/addr'
			})
		},
		//获取remark信息
		remark_desc(e) {
			this.remark = e.detail.value;
		},
		// 跳转到收货地址
		addAddress() {
			wx.navigateTo({
				url: '/pages/lottery/addr'
			})
		},
		//立即领取
		orderLottery() {
			let id = this.active_id;
			let remark = this.remark;
			console.log(this.currage)
			if(this.currage == 1) {
				//this.getTreaOrder(id, remark)  //积分夺宝
			}else if(this.currage == 2) {
				this.getZeroOrder(id, remark)  //免费抢购
			}
			
			
		},
		//跳转
		openPage1(id) {
			wx.navigateTo({
				url: '/pages/Indianapolis/info?id='+id
			})
		},
		openPage2(id) {
			wx.navigateTo({
				url: '/pages/panic/info?id='+id
			})
		},
		//立即领取 积分夺宝
		dogo1(id,ap_id) {
			this.isDialogShow = true;
			this.active_id = id;
			this.getAddrList();
		},
		 
		//ajax 立即领取 积分夺宝
		getTreaOrder(id, remark) {
			var _self = this;
			wx.showLoading({
				mask: true
			})
			util.request(api.getTreaOrder, {id: id, order_remark: remark},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					wx.showModal({
						title: '领取成功',
						showCancel: false,
						content: '3-7天将收到，请保持电话畅通！',
						success(res) {
							if(res.confirm) {
								_self.isDialogShow = false;
								_self.getTreaList()
							}
						}
					})
				}
			})
		},
		
		//立即领取 免费抢购
		dogo2(id,af_id) {
			this.isDialogShow = true;
			this.active_id = id;
			this.getAddrList();
		},
		// ajax 立即领取 免费抢购
		getZeroOrder(id, remark) {
			var _self = this;
			wx.showLoading({
				mask: true
			})
			util.request(api.getZeroOrder, {id: id, order_remark: remark},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					wx.showModal({
						title: '领取成功',
						showCancel: false,
						content: '3-7天将收到，请保持电话畅通！',
						success(res) {
							if(res.confirm) {
								_self.isDialogShow = false;
								_self.getZeroList()
							}
						}
					})
				}
			})
		},
		
		dealPrcture(str) {
			let newStr = str.split(',');
			return newStr[0]
		},
		close() {
			this.isDialogShow = false;
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
		
	},
	filters:{
		fromDateTime(times) {
			return util.formatTime(times)
		}
	}
}
